import React from 'react';

import QRCode from 'react-native-qrcode-svg';
import { TouchableOpacity, View } from 'react-native';

export const EOTA_LOGO_BLOB = require('./../assets/eota-qr-code-logo.png');

// TODO: more appealing color? (beware: farther from black ~= harder to scan)
const EOTA_GREEN = '#113422';

const QR_CODE_SIZE = 250;
const QR_CODE_LOGO_SIZE_MULTIPLIER = 0.4;

interface EotaCodeProps {
  value?: string;
  size?: number;
}

const EotaCode = ({ value, size }: EotaCodeProps) => (
  <View>
    <TouchableOpacity
      style={{
        borderRadius: 9,
        borderWidth: 0.7,
        borderColor: '#333',
        overflow: 'hidden',
      }}
    >
      <QRCode
        value={value}
        size={size || QR_CODE_SIZE}
        logo={EOTA_LOGO_BLOB}
        logoSize={(size || QR_CODE_SIZE) * QR_CODE_LOGO_SIZE_MULTIPLIER}
        color={EOTA_GREEN}
        backgroundColor="#fff"
        logoBackgroundColor="transparent"
        ecl={'Q'}
        getRef={null}
      />
    </TouchableOpacity>
  </View>
);

export default EotaCode;
