import React, { useContext, useState, useEffect } from 'react';
import { Redirect, Route } from './routing';

import routes from './client-routes';
import styles, { colors } from './styles';

import axios, { AxiosResponse, AxiosRequestConfig } from 'axios';
import {
  Platform,
  Alert,
  Keyboard,
  TouchableWithoutFeedback,
} from 'react-native';

import * as firebase from 'firebase/app';

export const EotaStylesContext = React.createContext({
  styles: styles,
  colors: colors,
});

type AlertFn = (msg: string, detail?: string) => void;

const alertFn = Platform.select<AlertFn>({
  android: Alert.alert,
  ios: Alert.alert,
  web: (msg, detail) => alert(msg + (detail ? ` -- ${detail}` : '')),
  default: (msg, detail) =>
    console.log(`tried to alert to the screen on unsupported platform`),
});

// prefer to not use this. it's intrusive (and annoying)
export const alertScreen: AlertFn = (msg, detail) => {
  console.log(`sending alert to screen, message: [${msg}], detail: [${detail}]`);
  alertFn(msg, detail);
};

interface IAuthContext {
  user: firebase.User | null;
  userIdToken: string | null;
  logOut: () => Promise<void>;
}

const DEFAULT_AUTH_CONTEXT: IAuthContext = {
  user: null,
  userIdToken: null,
  logOut: null,
};

export const AuthContext = React.createContext(DEFAULT_AUTH_CONTEXT);

// Code has format:
//   https://eota.me/code/[CODE_GOES_HERE]
//
// The reason it's a url rather than a raw code (which is what we ultimately want)
// is because we'd like to allow any qr-code scanner to scan it and send them
// to some kind of signup page or perhaps even 'checkout as guest' type of thing.
//
// TODO(nolan): perhaps pull from a config file / runtime env

const APPROVED_PAYCODE_URL_REGEX = RegExp(`https://eota.me/code/([^\\s]+)`);

interface IParsePaycodeResult {
  paycode: string;
  matchedUrl: string;
}

export function tryParsePaycode(url: string): [boolean, IParsePaycodeResult] {
  const match = APPROVED_PAYCODE_URL_REGEX.exec(url);
  const isApprovedPaycode = !!match;
  const result: IParsePaycodeResult = isApprovedPaycode
    ? {
        paycode: match[1],
        matchedUrl: match[0],
      }
    : null;
  return [isApprovedPaycode, result];
}

/**
 * Used just like Route, only this looks for and AuthContext and
 * redirects to login if no user is logged in
 **/
export const RouteAuthed = ({ path, ...rest }) =>
  useContext(AuthContext).user ? (
    <Route path={path} {...rest} />
  ) : (
    <Redirect
      to={{
        pathname: routes.login,
        state: { referrer: path },
      }}
    />
  );

export function useAxiosRequest(
  axiosRequestFn: (config: AxiosRequestConfig) => Promise<AxiosResponse<any>>,
  userIdToken: string,
  startingConfig?: AxiosRequestConfig,
  onFailure?: (error: any) => void,
): AxiosResponse<any> {
  const [response, setResponse] = useState(null);

  useEffect(() => {
    const fnString = JSON.stringify(axiosRequestFn);
    const source = axios.CancelToken.source();
    const newconfig = {
      ...startingConfig,
      headers: { Authorization: `Bearer ${userIdToken}` },
      timeout: 5000,
      cancelToken: source.token,
    };
    async function fetchData() {
      try {
        setResponse(await axiosRequestFn(newconfig));
      } catch (err) {
        if (axios.isCancel(err)) {
          console.log(
            `axios request programmatically cancelled. request function: ` +
              `[${fnString}], error: [${JSON.stringify(err)}]`,
          );
        } else {
          console.log(`panicking, got error [${JSON.stringify(err)}]`);
          onFailure(err);
        }
      }
    }
    fetchData();
    return () => source.cancel('axios request hook cleanup function called');
  }, [axiosRequestFn, startingConfig, userIdToken]);

  return response;
}

const isNative = Platform.OS === 'ios' || Platform.OS === 'android';

const dismissKeyboardIfNative = () => {
  if (isNative) Keyboard.dismiss();
};

export const KeyboardDismissable = (props) => (
  <TouchableWithoutFeedback onPress={dismissKeyboardIfNative}>
    {/* <KeyboardAvoidingView behavior="padding" {...props}> */}
    {props.children}
    {/* </KeyboardAvoidingView> */}
  </TouchableWithoutFeedback>
);
