import React, { useContext, useEffect, useRef, useState, Ref } from 'react';

import { TextInput, View, Platform, KeyboardAvoidingView } from 'react-native';
import { Button, Icon, Text } from 'react-native-elements';

import Delayed from './Delayed';
import LoadingIndicator from './LoadingIndicator';
import { Redirect } from '../routing';

import * as firebaseUtil from '../firebase-util';
import routes from '../client-routes';
import * as utils from '../utils';

const panic = (err) => {
  utils.alertScreen("We're sorry, something went wrong.", `${err.message}`);
  console.log(`panicking: error code [${err.code}], message [${err.message}]`);
};

const googleAuthOffered = Platform.select({
  android: true,
  ios: true,
  default: false,
});

const facebookAuthOffered = Platform.select({
  android: true,
  ios: true,
  default: false,
});

// TODO(below): instead of alerts, show the invalidities dynamically,
// like google material design input fields can do (i.e. w/ red underline & message)

const emailIsValid = (email) => {
  if (!email || !email.trim()) {
    utils.alertScreen('No email provided', 'Enter your email above.');
    return false;
  }

  email = email.trim();

  if (!/(^.+@.+\..+$)/.test(email)) {
    utils.alertScreen(
      'Invalid email format',
      'Email should look like "you@example.com".',
    );
    return false;
  }

  return true;
};

const stringHasAnyWhitespace = (str: string): boolean =>
  /[^\s]*\s[^\s]*/.test(str);

const passwordIsValid = (password) => {
  const passwordMinLength = 6;

  if (!password) {
    utils.alertScreen('No password provided', 'Enter your password above.');
    return false;
  }

  if (stringHasAnyWhitespace(password)) {
    utils.alertScreen('Password must not contain spaces.');
    return false;
  }

  if (password.length < passwordMinLength) {
    utils.alertScreen(
      `Password must be ${passwordMinLength} or more characters (no spaces).`,
    );
    return false;
  }

  return true;
};

const loginWithFacebook = (onCancel) =>
  firebaseUtil.authWithFacebook().then((result) => {
    if (result.cancelledByUser) onCancel();
    console.log(
      'logged in with facebook:',
      JSON.stringify(result.userCredential),
    );
  });

const loginWithGoogle = (onCancel) =>
  firebaseUtil
    .authWithGoogle()
    .then(({ cancelledByUser, userCredential }) => {
      if (cancelledByUser) return;
      console.log('logged in with google:', JSON.stringify(userCredential));
    })
    .catch((err) => {
      // getting inexplicable error messages (stemming from native code)
      // Issue created here: https://github.com/expo/expo/issues/6033

      // For now, just swallow the error
      console.log('inexplicable google signin error:', JSON.stringify(err));
      onCancel();
    });

const logUser = (user) =>
  console.log('logged in with email:', JSON.stringify(user));

export default function LoginScreen(props) {
  const { styles, colors } = useContext(utils.EotaStylesContext);

  const auth = useContext(utils.AuthContext);

  // TODO: will this scheme of redirection (potentially) cause a memory leak?
  const [authenticated, setAuthenticated] = useState(!!auth.user);
  useEffect(() => {
    setAuthenticated(!!auth.user);
  }, [auth.user]);

  const referrer: string = props.location.state
    ? props.location.state.referrer || routes.home
    : routes.home;

  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [signingUp, setSigningUp] = useState(false);

  const [indicateLoggingIn, setIndicateLoggingIn] = useState(false);
  const startLogin = () => setIndicateLoggingIn(true);
  const cancelLogin = () => setIndicateLoggingIn(false);

  const emailInputRef = useRef<TextInput>(null);
  const passwordInputRef = useRef<TextInput>(null);
  const focusPasswordInput = () => passwordInputRef.current.focus();

  const loginWith = (loginFn: () => Promise<void>) => {
    startLogin();
    return loginFn().catch((err) => {
      cancelLogin();
      panic(err);
    });
  };

  const submitEmailAndPasswordForm = () => {
    const loginEmail = email;
    const loginPassword = password;
    if (!emailIsValid(loginEmail) || !passwordIsValid(loginPassword)) return;
    setPassword(null);

    const loginFn = signingUp
      ? () => firebaseUtil.authSignUpWithEmail(loginEmail, loginPassword)
      : () => firebaseUtil.authLogInWithEmail(loginEmail, loginPassword);

    loginWith(() => loginFn().then(logUser));
  };

  const emailInput = (
    <EmailInput
      ref={emailInputRef}
      onChangeText={setEmail}
      // on linefeed, send to password field
      onSubmitEditing={focusPasswordInput}
    />
  );
  const passwordInput = (
    <PasswordInput
      ref={passwordInputRef}
      onChangeText={setPassword}
      // on linefeed, try to login
      onSubmitEditing={submitEmailAndPasswordForm}
    />
  );

  const loginForm = (
    <View>
      {emailInput}

      {passwordInput}

      <Button
        title="Login"
        titleStyle={styles.eotaButtonTitle}
        onPress={submitEmailAndPasswordForm}
        buttonStyle={styles.eotaButton}
        style={{ marginBottom: 5 }}
      />
      {/* Facebook login */}
      {facebookAuthOffered && (
        <Button
          title="Continue with Facebook"
          onPress={() => loginWith(() => loginWithFacebook(cancelLogin))}
          buttonStyle={{ ...styles.fbLoginButton }}
          titleStyle={{ color: colors.prettyBlue }}
          icon={{ type: 'feather', name: 'facebook', color: colors.prettyBlue }}
          iconContainerStyle={{ marginHorizontal: 10 }}
          style={{ borderWidth: 0 }}
        />
      )}
      {/* Google login */}
      {googleAuthOffered && (
        <Button
          title="Continue with Google"
          onPress={() => loginWith(() => loginWithGoogle(cancelLogin))}
          buttonStyle={styles.googleLoginButton}
          titleStyle={{ color: colors.googleRed }}
          icon={{
            type: 'font-awesome',
            name: 'google',
            color: colors.googleRed,
          }}
          iconContainerStyle={{ marginHorizontal: 11 }}
          style={{ borderWidth: 0 }}
        />
      )}
    </View>
  );

  const signupForm = (
    <View>
      {emailInput}
      {passwordInput}
      <Button
        title="Sign up"
        titleStyle={{ color: colors.buttonDarkText }}
        onPress={submitEmailAndPasswordForm}
        buttonStyle={styles.emailLoginButton}
        icon={{
          type: 'feather',
          name: 'arrow-right',
          color: colors.buttonDarkText,
        }}
        iconRight
        iconContainerStyle={{ marginHorizontal: 10 }}
      />
    </View>
  );

  // redirect back if/once a user is authenticated
  if (authenticated) return <Redirect push to={referrer} />;

  if (indicateLoggingIn) {
    return (
      <Delayed delayMs={150}>
        <View>
          <LoadingIndicator />
          <Text style={styles.paragraph}>logging you in...</Text>
        </View>
      </Delayed>
    );
  }

  return (
    <utils.KeyboardDismissable>
      <KeyboardAvoidingView behavior="padding" style={styles.container}>
        <View style={{ flex: 1, flexDirection: 'column' }}>
          {/* Title */}
          <View
            style={{
              flex: 5,
              margin: 10,
              marginTop: 35,
              justifyContent: 'flex-end',
            }}
          >
            <Icon type="feather" name="feather" size={45} />
            <Text style={styles.logoText}>eota</Text>
          </View>

          {/* Login / signup form */}
          <View style={{ flex: 8 }}>{signingUp ? signupForm : loginForm}</View>

          {/* Toggle login / signup */}
          <View style={{ flex: 1, justifyContent: 'flex-end' }}>
            {signingUp ? (
              <Button
                title="Log in instead"
                titleStyle={{ color: colors.prettyBlue }}
                onPress={() => setSigningUp(false)}
                buttonStyle={styles.transparentButton}
              />
            ) : (
              <Button
                title="Sign up"
                titleStyle={{ color: colors.prettyBlue }}
                onPress={() => setSigningUp(true)}
                buttonStyle={styles.transparentButton}
              />
            )}
          </View>
        </View>
      </KeyboardAvoidingView>
    </utils.KeyboardDismissable>
  );
}

interface FormInputParams {
  onChangeText: (password: string) => void;
  onSubmitEditing: () => void;
}

const extraNativePropsEmail = Platform.select<any>({
  ios: { textContentType: 'emailAddress' },
  android: { autoCompleteType: 'email' },
  default: null,
});

const EmailInput = React.forwardRef(
  ({ onChangeText, onSubmitEditing }: FormInputParams, ref: Ref<TextInput>) => {
    const { styles } = useContext(utils.EotaStylesContext);

    return (
      <TextInput
        placeholder="email"
        style={styles.loginFormTextInput}
        keyboardType="email-address"
        clearButtonMode="while-editing"
        autoCapitalize="none"
        returnKeyType="next"
        ref={ref}
        onChangeText={onChangeText}
        onSubmitEditing={onSubmitEditing}
        {...extraNativePropsEmail}
      />
    );
  },
);

const extraNativePropsPassword = Platform.select<any>({
  ios: { textContentType: 'password' },
  android: { autoCompleteType: 'password' },
  default: null,
});

const PasswordInput = React.forwardRef(
  ({ onChangeText, onSubmitEditing }: FormInputParams, ref: Ref<TextInput>) => {
    const { styles } = useContext(utils.EotaStylesContext);

    return (
      <TextInput
        placeholder="password"
        style={styles.loginFormTextInput}
        clearButtonMode="while-editing"
        autoCapitalize="none"
        secureTextEntry={true}
        returnKeyType="go"
        ref={ref}
        onChangeText={onChangeText}
        onSubmitEditing={onSubmitEditing}
        {...extraNativePropsPassword}
      />
    );
  },
);
