import React, { useEffect, useState, useContext, useMemo } from 'react';
import { StyleSheet, View, Linking, Platform } from 'react-native';
import { Button, Text } from 'react-native-elements';

import { BarCodeScanner } from 'expo-barcode-scanner';
import * as Permissions from 'expo-permissions';
import Icon from 'react-native-vector-icons/Feather';

import * as IntentLauncher from 'expo-intent-launcher';

import Delayed from './Delayed';
import * as utils from '../utils';

import Constants from 'expo-constants';
import LoadingIndicator from './LoadingIndicator';

const androidPkg = Constants.manifest.releaseChannel
  ? Constants.manifest.android.package // When published, considered as using standalone build
  : 'host.exp.exponent'; // In expo client mode

const openCameraSettingsAndroid = () =>
  IntentLauncher.startActivityAsync(
    IntentLauncher.ACTION_APPLICATION_DETAILS_SETTINGS,
    { data: 'package:' + androidPkg },
  );

const openCameraSettingsIOS = () => Linking.openURL('app-settings:');

const openCameraSettings = Platform.select({
  android: openCameraSettingsAndroid,
  ios: openCameraSettingsIOS,
  default: null,
});

const getCameraPermission = () =>
  Permissions.askAsync(Permissions.CAMERA)
    .then((response) => response.status === Permissions.PermissionStatus.GRANTED)
    .catch((err) => {
      const message = 'Unexpected error getting camera permission';
      const detail = `Try closing and reopening the app. 
Info: error code [${err.code}], error message [${err.message}]`;
      console.error(message, detail);
      utils.alertScreen(message, detail);
      return false;
    });

export default function BarcodeScanner(props) {
  const { styles } = useContext(utils.EotaStylesContext);
  const [hasCameraPermission, setHasCameraPermission] = useState(null);

  // TODO: will refreshing this every time be a performance hit? If so,
  // add `...}, []);` at the end to call only once. For background see:
  // https://reactjs.org/docs/hooks-reference.html#conditionally-firing-an-effect
  useEffect(() => {
    if (Platform.OS === 'web') setHasCameraPermission(false);
    else getCameraPermission().then((res) => setHasCameraPermission(res));
  });

  const handleBarCodeScanned = ({ type, data }) => {
    console.log(
      `Bar code scanned: type [${type}], data [${data}]. Emitting to parent.`,
    );
    props.emitResult({ type, data });
  };

  // based on camera permissions, choose what to display
  const whatToDisplay = useMemo(() => {
    switch (hasCameraPermission) {
      case null:
        // TODO: probably better to have a loading animation
        return (
          <Delayed delayMs={200}>
            <View>
              <LoadingIndicator />
              <Text style={styles.paragraph}>
                Requesting permission to access camera...
              </Text>
            </View>
          </Delayed>
        );
      case true:
        return (
          <BarCodeScanner
            onBarCodeScanned={handleBarCodeScanned}
            // only permit qr codes (only qr is supported on iOS according to:
            // https://docs.expo.io/versions/latest/sdk/bar-code-scanner/)
            barCodeTypes={[BarCodeScanner.Constants.BarCodeType.qr]}
            style={StyleSheet.absoluteFillObject}
          />
        );
      case false:
        return (
          <View style={styles.container}>
            <View style={{ margin: 35 }}>
              <Icon name="frown" size={75} style={{ textAlign: 'center' }} />
            </View>
            <Text style={styles.paragraph}>Unable to access the camera.</Text>
            {openCameraSettings ? (
              // if we know how to open camera settings, offer a button.
              <Button
                title="Open camera settings"
                onPress={openCameraSettings}
              />
            ) : (
              <Text style={styles.paragraph}>
                Enable camera access to continue.
              </Text>
            )}
          </View>
        );
      default:
        throw Error(
          `something strange happened getting camera permissions, return value [${hasCameraPermission}]`,
        );
    }
  }, [hasCameraPermission]);

  const cancelButton = (
    <Button
      title="Go back"
      onPress={props.onCancel}
      buttonStyle={styles.transparentButton}
    />
  );

  // TODO: Maybe also include manual code entry (e.g. a form with
  // https://eota.me/code/[enter-code-here])
  return hasCameraPermission === true ? (
    <View
      style={{
        ...StyleSheet.absoluteFillObject,
        flex: 1,
        flexDirection: 'column',
      }}
    >
      {whatToDisplay}
      <View style={{ flex: 1, justifyContent: 'flex-end' }}>{cancelButton}</View>
    </View>
  ) : (
    <View style={styles.container}>
      <View style={{ flex: 5, justifyContent: 'center' }}>{whatToDisplay}</View>
      <View style={{ flex: 1, justifyContent: 'flex-end' }}>{cancelButton}</View>
    </View>
  );
}
