/**
 * General Firebase actions
 */
// Can only be called once. Multiple calls (with same app name) are illegal and cause failure.
export const firebaseInit = () => {
  if (!firebase.apps.length) {
    // prevent double initialization (which is illegal) on hot reload
    const app = firebase.initializeApp(firebaseConfig);
  }
};

export const getCurrentUser = () => firebase.auth().currentUser;

export const onAuthStateChanged = (
  receiveNewUser: (a: firebase.User | null) => void,
) => void firebase.auth().onAuthStateChanged(receiveNewUser);

/**
 * See:
 * https://firebase.google.com/docs/auth/web/auth-state-persistence
 */
const withLocalPersistence = () =>
  firebase.auth().setPersistence(firebase.auth.Auth.Persistence.LOCAL);

/**
 * Email & password actions
 */
export const authSignUpWithEmail = (email, password) =>
  withLocalPersistence().then(() =>
    firebase.auth().createUserWithEmailAndPassword(email, password),
  );

export const authLogInWithEmail = (email, password) =>
  withLocalPersistence().then(() =>
    firebase.auth().signInWithEmailAndPassword(email, password),
  );

export const authSendPasswordResetEmail = (email) =>
  firebase.auth().sendPasswordResetEmail(email);

export const authSignOut = () => firebase.auth().signOut();

export const authDeleteCurrentUser = () => firebase.auth().currentUser.delete();

// Login with Facebook and keep the session in Firebase
export const authWithFacebook = async () =>
  Facebook.logInWithReadPermissionsAsync(facebookConfig.appId, {
    permissions: ['email', 'public_profile'],
  }).then(async (response) => {
    if (response.type === 'cancel') return { cancelledByUser: true };
    // response.type === 'success'
    const { token } = response;
    const oauthCredential = firebase.auth.FacebookAuthProvider.credential(token);
    return {
      cancelledByUser: false,
      userCredential: await firebase
        .auth()
        .signInWithCredential(oauthCredential),
    };
  });

export const authWithGoogle = () =>
  Google.logInAsync({
    androidClientId: googleConfig.androidClientId,
    androidStandaloneAppClientId: googleConfig.androidClientId,
    iosClientId: googleConfig.iosClientId,
    iosStandaloneAppClientId: googleConfig.iosClientId,

    // TODO: this paramater is required by ts from expo's package but is not
    // mentioned in API reference here:
    // https://docs.expo.io/versions/latest/sdk/google/
    //
    // github issue started: https://github.com/expo/expo/issues/6393
    clientId: 'no default client id',
    scopes: ['profile', 'email'],
  }).then((loginResult) => {
    if (loginResult.type === 'cancel')
      return Promise.resolve({ cancelledByUser: true, userCredential: null });

    // response.type === 'success'
    const { idToken, accessToken } = loginResult;
    const oauthCredential = firebase.auth.GoogleAuthProvider.credential(
      idToken,
      accessToken,
    );
    return firebase
      .auth()
      .signInWithCredential(oauthCredential)
      .then((userCredential) => {
        return { cancelledByUser: false, userCredential: userCredential };
      });
  });
