import React, { useState } from 'react';
import { Animated, View } from 'react-native';
import { ThemeProvider, FullTheme } from 'react-native-elements';

import { AppLoading } from 'expo';
import { Asset } from 'expo-asset';
import * as Font from 'expo-font';

import HomePage from './components/HomePage';
import LoginScreen from './components/LoginScreen';
import {
  StartTransaction,
  PayInStore,
  CheckBalance,
} from './components/Transactions';

import * as firebaseUtil from './firebase-util';
import {
  AuthContext,
  EotaStylesContext,
  RouteAuthed,
  alertScreen,
} from './utils';

import routes from './client-routes';
import { Redirect, Route, Switch, Router } from './routing';

import styles, { colors } from './styles';
import * as baseStyles from './styles';

const theme: Partial<FullTheme> = {
  Button: {
    buttonStyle: {
      ...baseStyles.eotaButtonBase,
    },
    titleStyle: {
      color: colors.buttonDarkText,
    },
  },
  colors: {
    primary: '#fff',
    secondary: '#999',
  },
};

firebaseUtil.firebaseInit();

const logOut = () =>
  firebaseUtil
    .authSignOut()
    .catch((err) =>
      alertScreen(
        `Error logging out: error code [${err.code}], message [${err.message}]`,
      ),
    );

const EotaApp = () => {
  const [user, setUser] = useState(firebaseUtil.getCurrentUser());
  const [userIdToken, setUserIdToken] = useState(null);

  firebaseUtil.onAuthStateChanged(async (user) => {
    // keep user & token always up to date with firebase auth
    setUser(user);
    if (!user) return;
    try {
      setUserIdToken(await user.getIdToken(/* forceRefresh */ true));
    } catch (err) {
      console.log(
        `could not update user token. error code: [${err.code}], message: [${
          err.message
        }], user: [${JSON.stringify(user)}]`,
      );
    }
  });

  return (
    <AuthContext.Provider
      value={{ user: user, userIdToken: userIdToken, logOut: logOut }}
    >
      <EotaStylesContext.Provider value={{ styles: styles, colors: colors }}>
        <ThemeProvider theme={theme}>
          <View
            style={{ flexDirection: 'row', flex: 1, justifyContent: 'center' }}
          >
            <FadeInView>
              <Router>
                <Redirect to={routes.home} />
                <Switch>
                  <Route path={routes.login} component={LoginScreen} />
                  <RouteAuthed path={routes.home} component={HomePage} />
                  <RouteAuthed path={routes.payInStore} component={PayInStore} />
                  <RouteAuthed
                    path={routes.startTransaction}
                    component={StartTransaction}
                  />
                  <RouteAuthed path={routes.balance} component={CheckBalance} />
                </Switch>
              </Router>
            </FadeInView>
          </View>
        </ThemeProvider>
      </EotaStylesContext.Provider>
    </AuthContext.Provider>
  );
};

const FadeInView = (props) => {
  const [fadeInAnimation] = useState(new Animated.Value(0));

  React.useEffect(() => {
    Animated.timing(fadeInAnimation, { toValue: 1, duration: 600 }).start();
  }, []);

  return (
    <Animated.View style={{ ...styles.container, opacity: fadeInAnimation }}>
      {props.children}
    </Animated.View>
  );
};

const BLOBS = [
  require('./assets/eota-qr-code-logo.png'),
  require('./assets/eota-qr-code-logo-full-splash.png'),
  require('./assets/feather-1024-white-background.png'),
];

function AsyncLoadApp(props) {
  const [assetsReady, setAssetsReady] = useState(false);
  const loadAssets = async () => {
    await Promise.all(
      BLOBS.map((blob) => Asset.fromModule(blob).downloadAsync()),
    );
    await Font.loadAsync({
      'montserrat-extra-light': require('./assets/Montserrat-ExtraLight.ttf'),
      'montserrat-medium': require('./assets/Montserrat-Medium.ttf'),
    });
  };

  if (!assetsReady) {
    return (
      <AppLoading
        startAsync={loadAssets}
        onFinish={() => setAssetsReady(true)}
        onError={console.warn}
      />
    );
  }

  return <props.component />;
}

export default function App() {
  return <AsyncLoadApp component={EotaApp} />;
}
