import React, { useContext, useState } from 'react';
import { Text, View } from 'react-native';
import { Button, Icon } from 'react-native-elements';

import routes from '../client-routes';
import Delayed from './Delayed';
import * as utils from '../utils';
import LoadingIndicator from './LoadingIndicator';
import { LinkButton } from '../routing-common';

export default () => {
  const authContext = useContext(utils.AuthContext);
  const { styles } = useContext(utils.EotaStylesContext);
  const [loggingOut, setLoggingOut] = useState(false);

  if (loggingOut) {
    authContext.logOut();
    return (
      <Delayed delayMs={100}>
        <View>
          <LoadingIndicator />
          <Text style={styles.paragraph}>Logging out...</Text>
        </View>
      </Delayed>
    );
  }

  return (
    <View style={styles.container}>
      <View style={{ flex: 4, marginBottom: 20, justifyContent: 'flex-end' }}>
        <Icon type="feather" name="feather" size={45} />
        <Text style={styles.logoText}>eota</Text>
      </View>
      {/* Actions */}
      <View style={{ flex: 5, justifyContent: 'flex-start' }}>
        {/* Scan code */}
        <LinkButton
          to={routes.payInStore}
          title="Scan a pay code"
          icon={{ type: 'material-community', name: 'qrcode-scan' }}
          iconContainerStyle={{ marginHorizontal: 10 }}
        />

        {/* Start transaction */}
        <LinkButton
          to={routes.startTransaction}
          title="Start a transaction"
          icon={{ type: 'feather', name: 'edit' }}
          iconContainerStyle={{ marginHorizontal: 10 }}
        />

        {/* Balance */}
        <LinkButton
          to={routes.balance}
          title="Balance"
          icon={{ type: 'feather', name: 'dollar-sign' }}
          iconContainerStyle={{ marginHorizontal: 5 }}
        />

        {/* Log out */}
        <View style={{ flex: 1, justifyContent: 'flex-end' }}>
          <Button
            title="Log out"
            onPress={() => setLoggingOut(true)}
            icon={{ type: 'feather', name: 'arrow-left', size: 20 }}
            iconContainerStyle={{ marginHorizontal: 10 }}
          />
        </View>
      </View>
    </View>
  );
};
