const EOTA_CODE_PREFIX = 'https://eota.me/code/';
const API_BASEURL = 'https://us-central1-eota-gcp.cloudfunctions.net/api';

interface ReqCreateTxnBody {
  amount: string;
  desc: string;
  currency_code: string;
}

const makeApiError = async (res: Response) =>
  new ApiError(res, res.status, await res.json());

class ApiError extends Error {
  status: number;
  responseJson: any;
  response: Response;
  constructor(response, status, responseJson) {
    super('Bad response from eota api: ');
    this.name = 'ApiError';
    this.status = status;
    this.responseJson = responseJson;
    this.response = response;
  }
}

const alertApiError = (err: any) => {
  console.log('API call failure:', err);
  const errorResponse = ((err && err.responseJson) ||
    null) as ApiErrorResponse | null;
  const errorMessage =
    (errorResponse && errorResponse.errorMessage) ||
    'something unexpected happened';

  alertScreen('Error:', errorMessage);
};

const getHeaders = (token: string) => ({
  'Content-Type': 'application/json',
  Authorization: `Bearer ${token}`,
  Accept: 'application/json',
});

const eotaApiCreateTransaction = async (
  { body, token }: { body: ReqCreateTxnBody; token: string },
  { signal },
) => {
  const res = await fetch(`${API_BASEURL}/transaction`, {
    method: 'POST',
    body: JSON.stringify(body),
    headers: getHeaders(token),
    signal,
  });
  if (!res.ok) throw await makeApiError(res);
  return res.json();
};

const getDateString = (timeUsec: number) => new Date(timeUsec).toLocaleString();

