const AddMoneyView = ({ onCancel }) => {
  const { user: authUser, userIdToken } = useContext(AuthContext);
  const { styles, colors } = useContext(EotaStylesContext);
  const p = styles.paragraph;

  const [amount, setAmount] = useState('');

  const { data, setData, error, isPending, run } = useFetch(
    `${API_BASEURL}/user/${authUser.uid}/balance`,
    {
      method: 'POST',
      headers: getHeaders(userIdToken),
    },
  );

  const [json, setJson] = useState<UserModel | null>(null);

  const tryAddMoney = async () => {
    const amountToAdd = amount;
    if (!amountToAdd) {
      alertScreen('Must provide a dollar amount');
      return;
    }
    if (!/[1-9]/.test(amountToAdd)) {
      alertScreen(`Must be at least $0${SEP}01`);
      return;
    }
    const errorMsg = getDollarsErrorMsg(amountToAdd);
    if (errorMsg) {
      alertScreen(errorMsg);
      return;
    }

    run({ body: JSON.stringify({ addition: amountToAdd }) });
  };
  const getjson = async () => {
    setJson(await data.json());
  };

  if (error) {
    alertApiError(error);
    setData(null);
  } else if (isPending) {
    return (
      <Delayed delayMs={150}>
        <View>
          <LoadingIndicator />
          <Text style={p}>transferring money to your account...</Text>
        </View>
      </Delayed>
    );
  } else if (data && json) {
    const user = data as UserModel;
    return (
      <View>
        <Icon
          type="feather"
          name="check-circle"
          size={50}
          color={colors.prettyBlue}
          style={{ fontWeight: '300' }}
        />
        <Text
          style={{ ...p, margin: 20 }}
        >{`Total balance now $${user.balanceUSD}.`}</Text>
      </View>
    );
  }

  return (
    <View style={{ ...styles.container, flex: 10, justifyContent: 'center' }}>
      <View style={{ flex: 1, justifyContent: 'flex-end' }}>
        <DollarsInput onChangeText={setAmount} />
      </View>

      <View style={{ flex: 1, justifyContent: 'flex-start' }}>
        <Button
          onPress={tryAddMoney}
          title="Submit"
          buttonStyle={styles.commitButton}
          titleStyle={{ color: '#fff' }}
          icon={{
            type: 'feather',
            name: 'credit-card',
            color: '#fff',
            size: 21,
          }}
          iconRight
          iconContainerStyle={{
            marginLeft: 12,
            paddingTop: 1,
          }}
        />
        <Button
          title="Cancel"
          onPress={onCancel}
          style={{ margin: 10 }}
          icon={{ type: 'feather', name: 'corner-left-down', size: 21 }}
          iconContainerStyle={{ marginRight: 12 }}
        />
      </View>
    </View>
  );
};

