import React, { useState, useContext } from 'react';
import { View } from 'react-native';

import useTimeout from 'use-timeout';

import { EotaStylesContext } from '../utils';

interface DelayedProps {
  delayMs: number;
  children: any;
}

const Delayed = (props: DelayedProps) => {
  const { styles } = useContext(EotaStylesContext);
  const [delayFinished, setDelayFinished] = useState(false);

  useTimeout(() => setDelayFinished(true), props.delayMs);

  return delayFinished && <View style={styles.container}>{props.children}</View>;
};

export default Delayed;
