import React from 'react';
import { View } from 'react-native';
import {
  MaterialIndicator,
  MaterialIndicatorProps,
} from 'react-native-indicators';

const PALE_BLUE = 'rgba(56,151,241,0.4)';

const LoadingIndicator = (props: MaterialIndicatorProps) => (
  <View style={{ margin: 30 }}>
    <MaterialIndicator style={{ margin: 20 }} color={PALE_BLUE} {...props} />
  </View>
);

export default LoadingIndicator;
